@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue&display=swap');

*,
html {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
  box-sizing: border-box;
}
body{
  background-color: aliceblue;
  height: 100vh;
}
.main{
  height: 200vh;
}
.icon{
  background-image: url(https://pbs.twimg.com/media/Fs7PQNWaMAA0GLi?format=webp);
  width: 100%;
  height: 50px;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  
}
.Nav-main{
  position: fixed;
  width: 100%;
  background-color: rgb(230, 230, 230, 0.6);
}
.Nav-v > li{
  text-decoration: none;
  list-style: none;
  color: gray;
  padding: 8px;
  padding-top: 3px;
  padding-bottom: 3px;
  border-radius: 5px;
}
.Nav-v > li:hover{
  background-color: rgb(193, 192, 192, 0.6);
  transition: ease 0.3s;
}
.Nav-bg{
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(193, 192, 192);
  backdrop-filter: blur(20px);
}
.Nav-v{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  margin-bottom: 2px;
  cursor: pointer ;
}
.cari{
  padding: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
}

